*{
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	text-decoration: none; 
/*	outline: 1px solid gray;
*/}

body{
	background-color: white
}

.header__buttons{
	background-color: white;
	width: auto;
	height: 50px;
	max-width: 1280px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	margin-top: 20px;
	margin-bottom:  20px;
	border-bottom: 2px solid black;
	border-top:  2px solid black;
}

.header__blocks{
	width: 70px;
	height: 70px;
	padding: 2px;
	margin:  19px;
	color: black;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
}
.header__blocks__red{
	width: 70px;
	height: 70px;
	padding: 2px;
	margin:  19px;
	color: black;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
}


.header__blocks__terms{
	border-right:  1px solid white;
	width: 165px;
	height: 20px;
	color:black;
	margin:  19px;
		padding: 2px;
		display: -ms-flexbox;
		display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
}

.header__blocks__blue{
	width: 70px;
	height: 20px;
	padding: 2px;
	margin: 4px 2px 2px 2px;
	color: black;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	cursor:pointer;
}

.header__blocks__blue__linea{
	width: 50px;
	height: 20px;
	padding: 2px;
	margin: 4px 2px 2px 2px;
	color: black;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	border-right:  1px solid white;
}

#logo__white{
	width: 150px;
	height: 150px;
	-ms-flex-item-align: center;
	    align-self: center;
	margin-left: -25px;
}

 .text{
 	
 	font-weight: 400;
 	margin: 0px ;
 	font-family: Courier New;
	font-size: 14px;
	text-align: center;

	text-transform: uppercase;
	font-stretch: ultra-condensed
 }

 .text__red{
 	color: red;
  	font-weight: 400;
 	margin:0;
 	font-family: Courier New;
	font-size: 14px;
	text-align: center;
	line-height: 33px;
	text-transform: uppercase;
 }
.shopping_cart{
	border:1px solid yellow;
	color: /*#2a7fca*/ yellow;
	margin-left: 20px;
	font-size: 40px;
	background-color: black;
	height: 70%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	padding-left: 10px;
	width: 60px;
}

.fa.fa-shopping-cart{
	background-color: inherit;
	-ms-flex-item-align: start;
	    align-self: flex-start;
	padding-bottom: 10px;
	color: black;
	margin-top:  -3px;
	margin-left: 10px;
}











