*{
	font-family: Courier New;
}

.body__landing{
	background-color: white;
	width: 100%;
	height: auto;
	max-width: 1280px;
	position: static;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
}

#landing__image{
	width: 1271px;
}


.body__featured{	
	background-color: white;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-direction: column;
	    flex-direction: column;
	display: -ms-flexbox;
	display: flex;
	/*border:1px solid lightgray;*/
	color: black;
	font-family: Courier New;
	height: 91px;
	width: 100%;
	margin-bottom: 0px;
	margin-left: 0px;
}
.featured{
	width: 70%;
	color: black;
	font-family: Courier New;
	font-size: 30px;
	text-align: center;
	-ms-flex-item-align: center;
	    align-self: center;
}
.featured__uno{
	margin: 0;
	margin-left: 20px;
	color: darkslategrey;
}
.packer{
	border-radius: 5px;
	width: 1280px;  /*aqui*/
	margin-left:00px;  /*aqui*/
	height: auto;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	margin-bottom: 20px;
	-ms-flex-line-pack: start;
	    align-content: flex-start;
}
.block{
/*	border:1px solid gray;*/
	border-radius: 5px;
	margin: 15px;
	width: 390px;
	height: 450px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: start;
	    align-items: flex-start;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: end;
	    justify-content: flex-end;
/*	box-shadow: 10px 10px 5px grey;*/
}
.text__block{
	margin: 0;
	margin-left: 20px;
	font-size: 13px;
	font-weight: 600;
}
.text__block__gray{
	margin: 0;
	margin-left: 20px;
	color: gray;
	font-size: 13px;
	font-weight: 600;
}
.url__api{
	margin-left: 20px;
	width: 330px;
	height: 300px;
	max-width: 330px;
	max-height: 330px;
}
.block__image{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: start;
	    justify-content: flex-start;
}

.block__under{
	height: 100px;
	width: 330px;
	margin-top: 30px;
	margin-left: 20px;
	margin-bottom: 10px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-direction: column;
	    flex-direction: column;
	background-color: #2c2c2b;
}

.text__under{
	margin: 0;
	color: white;
	text-align: center;
}

.text__under__price{
	margin: 0;
	color: #ff69b4; /*green*/
	text-align: center;
}









